import { Component, OnInit } from '@angular/core';
import { ToDo } from '../to-do';
import { ToDos } from '../to-do-mock';

@Component({
  selector: 'app-to-do',
  templateUrl: './to-do.component.html',
  styleUrls: ['./to-do.component.css'],
})
export class ToDoComponent implements OnInit {
  todos = ToDos;
  addedToDo = '';

  constructor() {}

  ngOnInit(): void {}

  addToDo(): void {
    if (this.addedToDo === '') {
      alert('Please enter a notes!!!');
      return;
    }
    ToDos.push({
      id: ToDos[ToDos.length - 1].id + 1,
      todo: this.addedToDo,
      completed: false,
      isDisplay : true
    });
    this.addedToDo = '';
    console.log(ToDos);
  }
}
